h2 {
  margin: 0 0 1px;
  font-size: medium;
}

.card {
  width: 80%;
  background-color: azure;
  margin: 1rem auto;
  border-style: solid;
  border-color: black;
  line-height: 5px;
  display: flex;
}

.card img {
  width: 50px;
  float: left;
  margin: 15px;
}

.card button {
  border: 0;
  background-color: azure;
  width: min-content;
  float: right;
  margin: auto;
}
.content {
  padding: 20px;
  flex: 2;
  text-align: left;
}

.content p {
  padding-right: 10px;
  display: inline-block;
  font-size: x-small;
}
